$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;
$close-but: #E86C62;

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

.todo-type {
    display: flex;
    flex-wrap: wrap;
    padding: 10px 0;
    justify-content: space-between;
    background-color: transparent;
    width: 100%;
    .title {
        font-weight: 500;
        font-size: 16px;
        height: 30px;
        line-height: 30px;
        @include tablet-desktop {
            font-size: 20px;
            height: 40px;
            line-height: 40px;
        }
    }
    .button {
        width: 80px;
        height: 30px;
        margin-left: 10px;
        border: none;
        border-radius: 8px;
        font-size: 14px;
        font-weight: 500;
        font-family: "Montserrat", sans-serif;
        color: #fff;
        @include tablet-desktop {
            width: 100px;
            height: 40px;
            font-size: 16px;
            font-weight: 400;
        }
        &:hover {
            cursor: pointer;
        }
        &.done {
            background-color: $add-color;
            &:hover {
                background-color: #fff;
                color: $add-color;
            }
        }
        &.delete {
            background-color: $close-but;
            &:hover {
                background-color: #fff;
                color: $close-but;
            }
        }
    }
    .list-todo {
        width: 100%;
        min-height: 100px;
        padding: 10px 20px;
        margin-top: 10px;
        background-color: #fff;
        border-radius: 10px;
    }
}