$tablet-width:  740px;
$desktop-width: 1023px;
$homepage-color: #9DC3E6;
$todo-color: #FFD966;
$pomodoro-color: #F4B183;

// Mixin
@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin p-t {
    padding-top: 80px;
    min-height: calc(100vh - 40px - 80px);
    @include desktop {
        min-height: calc(100vh - 66px - 25px);
        padding-top: 100px;
    }
}

/* --------- Mobile: 740px <= width < 1023px ---------*/
@mixin mobile { @media (max-width: #{$tablet-width}) {       
    @content;   
  } 
}
/* --------- Tablet: 740px <= width < 1023px ---------*/
@mixin tablet { @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width}) {       
    @content;   
  } 
}  
/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

#homepage {
    @include p-t;
    @include f-column;
    padding-bottom: 50px;
    background-color: $homepage-color;
    &>div {
        margin: auto;
        &>div {
            @include f-center;
            padding: 30px 0;
        }
        .title {
            color: white;
            font-size: 20px;
            text-align: center;
            @include tablet-desktop {
                font-size: 24px
            }
        }
        .nav {
            @include f-column;
            @include tablet-desktop {
                @include f-center;
                flex-direction: row
            }
            div {
                @include f-center;
                margin: 0 0 40px 0;
                @include tablet-desktop {
                    margin: 0 20px;
                }
                a {
                    text-align: center;
                    width: 150px;
                    height: 70px;
                    line-height: 70px;
                    border-radius: 15px;
                    color: gray;
                    font-size: 18px;
                    font-weight: 500;
                    @include tablet-desktop {
                        font-size: 24px;
                        width: 200px;
                        height: 80px;
                        line-height: 80px;
                        border-radius: 15px;
                    }
                    &.todo {
                        background-color: $todo-color;
                    }
                    &.pomodoro {
                        background-color: $pomodoro-color;
                    }
                    &:hover {
                        cursor: pointer;
                        background-color:white;
                    }
                }
            }
        }
    }
}
