$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;

// Mixin
@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin p-t {
    padding-top: 80px;
    min-height: calc(100vh - 40px - 80px);
    @include desktop {
        min-height: calc(100vh - 66px - 25px);
        padding-top: 100px;
    }
}

/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

#todolist {
    @include f-column;
    @include p-t;
    padding-bottom: 50px;
    background-color: $todo-color;
    align-items: center;
    &>div {
        margin: 10px 0
    }
    .title {
        @include f-center;
        color: white;
        font-size: 18px;
        margin: 20px 0;
        @include tablet-desktop {
            font-size: 24px
        }
    }
    .add-todo {
        @include f-center;
        flex-wrap: wrap;
        justify-content: space-around;
        width: 80vw;
        max-width: 600px;
        &>* {
            margin: 10px 5px;
            font-family: "Montserrat", sans-serif;
        }
        .add-input {
            width: 70%;
            min-width: 250px;
            padding: 5px;
            font-size: 16px;
            border-radius: 5px;
            border: none;
            outline: none;
            @include tablet-desktop {
                font-size: 18px;
            }
        }
        .add-level {
            width: 20%;
            min-width: 70px;
            padding: 5px;
            border-radius: 5px;
            border: none;
            outline: none;
            @include tablet-desktop {
                font-size: 18px;
            }        
            &:hover {
                cursor: pointer;
            }
        }
        .add-but {
            width: 25%;
            min-width: 100px;
            max-width: 150px;
            height: 40px;
            border-radius: 15px;
            border: none;
            background-color: $add-color;
            color: #FFFFFF;
            font-size: 16px;
            font-weight: 500;
            &:hover {
                cursor: pointer;
                background-color: #FFFFFF;
                color: $add-color;
            }
            @include tablet-desktop {
                font-size: 20px;
            }        
        }
    }
    .todo {
        @include f-column;
        width: 80vw;
        max-width: 600px;
        &>div {
            width: 100%;
        }
    }
}