$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;
$close-but: #E86C62;
$edit-but: #B4BCB6;

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

.task {
    display: flex;
    justify-content: space-between;
    padding: 5px 0;
    .task-content {
        height: 35px;
        line-height: 35px;
        font-size: 16px;
        @include tablet-desktop {
            height: 40px;
            line-height: 40px;
            font-size: 20px;
        }
    }
    .edit-task {
        padding: 0 5px;
        width: 60%;
        border: 1px solid $edit-but;
        border-radius: 5px;
        font-size: 16px;
        font-family: "Montserrat", sans-serif;
        @include tablet-desktop {
            width: 60%;
            font-size: 18px;
        }
    }
    .button {
        width: 35px;
        height: 35px;
        margin-left: 10px;
        border: none;
        border-radius: 8px;
        font-size: 12px;
        &:hover {
            cursor: pointer;
        }
        @include tablet-desktop {
            width: 50px;
            height: 40px;
            font-size: 16px;
        }
        &.done {
            background-color: $add-color;
            &:hover {
                background-color: #fff;
                color: $add-color;
            }
        }
        &.edit, &.cancel {
            background-color: $edit-but;
            &:hover {
                background-color: #fff;
                color: $edit-but;
            }
        }
        &.delete {
            background-color: $close-but;
            &:hover {
                background-color: #fff;
                color: $close-but;
            }
        }
    }
}