$tablet-width:  740px;
$desktop-width: 1023px;
$footer-color: #FFFFFF;

// Mixin

@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin f-end {
    display: flex;
    justify-content: end;
}

@mixin f-left {
    display: flex;
    justify-content: left;
}

@mixin button-nav {
    width: 30px;
    border: none;
    border-radius: 15%;
}

/* --------- Mobile: 740px <= width < 1023px ---------*/
@mixin mobile { @media (max-width: #{$tablet-width}) {       
    @content;   
  } 
}
/* --------- Tablet: 740px <= width < 1023px ---------*/
@mixin tablet { @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width}) {       
    @content;   
  } 
}  
/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

#footer {
    background-color: $footer-color;
    display: flex;
    flex-direction: column-reverse;
    padding: 10px 0 10px 20px;
    font-size: 14px;
    @include tablet {
        font-size: 16px;
    }
    @include desktop {
        padding: 10px 0 20px 0;
        flex-direction: row;
        justify-content: space-between;
        font-size: 18px;
    }
    .copyright {
        @include f-column;
        justify-content: center;
        font-style: italic;
        padding: 20px 10px 10px 10px;
        overflow: hidden;
    }
    .contact {
        display: flex;
        padding-right: 20px;
        flex-wrap: wrap;
        overflow: hidden;
        &>div div:first-child {
            font-weight: 600;
        }
        &>div>div {
            padding: 10px 10px 0 10px;
        }
        .call {
            @include f-center;
            flex-direction: column;
            order: 0;
            @include desktop {
                align-items: center;
            }
        }
        .write {
            @include f-center;
            flex-direction: column;
            order: 2;
            @include desktop {
                order: 1;
                align-items: center;
            }
        }
        .follow {
            @include f-center;
            flex-direction: column;
            order: 1;
            @include desktop {
                align-items: center;
            }
            .icons {
                @include f-left;
                @include desktop {
                    @include f-center;
                }
                div {
                    @include f-center;
                    a {
                        padding-right: 20px;
                        font-size: 16px;
                        font-weight: bold;
                        @include desktop {
                            padding: 0 10px;
                            font-size: 18px;
                        }
                    }
                }
            }
        }
    }
}