$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;
$pomodoro-color: #F4B183;
$close-but: #E86C62;
$edit-but: #B4BCB6;

// Mixin
@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin p-t {
    padding: 80px 0 50px 0;
    min-height: calc(100vh - 40px - 80px);
    @include desktop {
        min-height: calc(100vh - 66px - 25px);
        padding-top: 100px;
    }
}

/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

.set-time {
    @include f-center;
    align-items: center;
    .set-content {
        width: 100px;
        height: 80px;
        margin: 0 10px;
        text-align: center;
        line-height: 80px;
        background-color: #fff;
        color: black;
        border-radius: 10px;
        font-size: 30px;
        font-weight: 600;
        @include tablet-desktop {
            width: 120px;
            height: 100px;
            line-height: 100px;
            font-size: 50px;
            border-radius: 15px;
        }
    }
    button {
        width: 50px;
        height:50px;
        border: none;
        border-radius: 50%;
        font-size: 18px;
        font-weight: 800;
        @include tablet-desktop {
            font-size: 20px;
        }
        &.minus {
            background-color: $close-but;
            color: #fff;
            &:hover {
                cursor: pointer;
                background-color: #fff;
                color: $close-but;
            }
        }
        &.plus {
            background-color: $add-color;
            color: #fff;
            &:hover {
                cursor: pointer;
                background-color: #fff;
                color: $add-color ;
            }
        }
    }
}