$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;
$pomodoro-color: #F4B183;
$close-but: #E86C62;
$edit-but: #B4BCB6;

// Mixin
@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin p-t {
    padding: 80px 0 50px 0;
    min-height: calc(100vh - 40px - 80px);
    @include desktop {
        min-height: calc(100vh - 66px - 25px);
        padding-top: 100px;
    }
}

/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

#pomodoro {
    @include f-column;
    @include p-t;
    align-items: center;
    background-color: $pomodoro-color;
    .title {
        @include f-center;
        color: white;
        font-size: 18px;
        margin: 20px 0 40px 0; 
        @include tablet-desktop {
            font-size: 24px
        }
    }
    .pomodoro-content {
        @include f-column;
        align-items: center;
        width: 80vw;
        max-width: 600px;
        .pomodoro-set {
            @include f-center;
            justify-content: space-around;
            flex-wrap: wrap;
            width: 90%;
            .set-time {
                @include f-column;
                align-items: center;
                padding: 10px 0;
                margin: 0 12px;
                width: 45%;
                min-width: 200px;
                font-size: 20px;
                font-weight: 500;
                color: white;
                &>div {
                    margin: 10px 0;
                    font-size: 20px;
                    @include tablet-desktop {
                        font-size: 24px
                    }
                }
            }
        }
        .set-but {
            @include f-column;
            align-items: center;
            padding: 10px;
            margin: 20px 0;
            font-family: "Montserrat", sans-serif;
            .button {
                width: 150px;
                border: none;
                border-radius: 8px;
                font-size: 16px;
                font-weight: 500;
                margin: 5px 0;
                font-family: "Montserrat", sans-serif;
                &:hover {
                    cursor: pointer;
                }
                @include tablet-desktop {
                    font-size: 20px;
                }
                &.edit {
                    height: 50px;
                    background-color: $todo-color;
                    &:hover {
                        background-color: #fff;
                        color: $todo-color;
                    }
                    @include tablet-desktop {
                        width: 150px;
                        height: 70px;
                    }
                }
                &.cancel {
                    height: 30px;
                    background-color: $close-but;
                    &:hover {
                        background-color: #fff;
                        color: $close-but;
                    }
                    @include tablet-desktop {
                        width: 150px;
                        height: 40px;
                    }
                }
            }
        }

        .time {
            display: flex;
            flex-direction: column-reverse;
            align-items: center;
            width: 100%;
            .set-again-but {
                display: flex;
                justify-content: end;
                width: 80%;
                button {
                    width: 80px;
                    height: 30px;
                    border: none;
                    border-radius: 10px;
                    background-color: $edit-but;
                    color: #fff;
                    font-family: "Montserrat", sans-serif;
                    font-size: 16px;
                    font-weight: 500;
                    @include tablet-desktop {
                        width: 100px;
                        height: 40px;
                        font-size: 18px;
                    }
                    &:hover {
                        cursor: pointer;
                        background-color: #fff;
                        color: $edit-but;
                    }
                }
            }
        }
    }
}