$tablet-width:  740px;
$desktop-width: 1023px;
$todo-color: #FFD966;
$add-color: #92D050;
$pomodoro-color: #F4B183;
$close-but: #E86C62;
$edit-but: #B4BCB6;

// Mixin
@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin p-t {
    padding: 80px 0 50px 0;
    min-height: calc(100vh - 40px - 80px);
    @include desktop {
        min-height: calc(100vh - 66px - 25px);
        padding-top: 100px;
    }
}

/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

/* --------- Tablet & Desktop: 740px <= width  ---------*/
@mixin tablet-desktop { @media (min-width: #{$tablet-width}) {       
    @content;   
  } 
}

.contain {
    @include f-column;
    align-items: center;
    width: 80%;
    @include tablet-desktop {
        margin-bottom: 50px;
    }
    .title {
        font-size: 20px;
        font-weight: 600;
        padding: 15px;
        @include tablet-desktop {
            font-size: 24px;
        }
    }
    .time {
        margin: 10px 0 20px 0;
        width: 100%;
        height: 120px;
        text-align: center;
        line-height: 120px;
        background-color: #fff;
        border-radius: 15px;
        font-size: 50px;
        font-weight: 600;
        @include tablet-desktop {
            height: 180px;
            line-height: 180px;
            font-size: 60px;
        }
    }
    .but {
        @include f-center;
        width: 100%;
        flex-wrap: wrap;
        button {
            width: 70px;
            height: 70px;
            margin: 10px 8%;
            border: none;
            border-radius: 50%;
            font-size: 25px;
            text-align: center;
            @include tablet-desktop {
                width: 100px;
                height: 100px;
                margin: 10px 5%;
                font-size: 35px;
            }
        }
        .start {
            background-color: $add-color;
            color: #fff;
            &:hover {
                cursor: pointer;
                background-color: #fff;
                color: $add-color;
            }
        }
        .stop {
            background-color:$close-but;
            color: #fff;
            &:hover {
                cursor: pointer;
                background-color: #fff;
                color: $close-but;
            }
        }
        .reset {
            background-color: $todo-color;
            color: #fff;
            &:hover {
                cursor: pointer;
                background-color: #fff;
                color: $todo-color;
            }
        }
    }
}