$tablet-width:  740px;
$desktop-width: 1023px;
$header-color: #FFFFFF;
$homepage-color: #9DC3E6;
$todo-color: #FFD966;
$pomodoro-color: #F4B183;
$close-but: #E86C62;

// Mixin

@mixin f-center {
    display: flex;
    justify-content: center;
}

@mixin f-column {
    display: flex;
    flex-direction: column;
}

@mixin f-end {
    display: flex;
    justify-content: end;
}

@mixin button-nav($hover-but) {
    width: 40px;
    border: none;
    border-radius: 15%;
    background-color: #C8C6C6;
    &:hover {
        cursor: pointer;
        background-color: $hover-but;
        color: #FFFFFF;
    }
    @include tablet {
        width: 48px;
        font-size: 24px;
    }
}

/* --------- Mobile: width < 740px ---------*/
@mixin mobile { @media (max-width: #{$tablet-width}) {       
    @content;   
  } 
}
/* --------- Tablet: 740px <= width < 1023px ---------*/
@mixin tablet { @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width}) {       
    @content;   
  } 
}  
/* --------- Desktop: 1024px <= width --------- */
@mixin desktop { @media (min-width: #{$desktop-width}) {     
   @content;  
  } 
}

#header {
    display: flex;
    position: fixed;
    z-index: 999;
    width: 100vw;
    background-color: $header-color;
    font-size: 18px;
    font-weight: 500;
    &.show-but {
        justify-content: end;
        div {
            @include f-center;
            margin: 0 10px;
            flex-direction: column;
            button {
                @include button-nav($homepage-color);
                height: 80%;
                font-size: 20px;
            }
        }
    }
    &.show-nav {
        justify-content: center;
        @include mobile {
            height: 100vh;
        }
        @include tablet {
            height: 100vh;
        }
    }
    @include mobile {
        height: 50px;
    }
    @include tablet {
        height: 60px;
    }
    @include desktop {
        top: 0;
        left: 0;
        font-size: 22px;
    }
    .ul-header {
        @include f-column;
        width: 100%;
        @include desktop {
            @include f-center;
            flex-direction: row;
            width: 70%;
            max-width: 1000px;
        }
        div {
            @include f-end;
            button {
                @include button-nav($close-but);
                margin: 10px;
                height: 40px;
                background-color: $close-but;
                color: #FFFFFF;
                @include tablet{
                    height: 48px;
                }
            }
        }
        li {
            text-align: center;
            @include desktop {
                width: 33%;
            }
            &:hover {
                background-color: #fff;
            }
            a {
                display: block;
                padding: 20px 50px;
            }
            .homepage-active {
                background-color: $homepage-color;
            }
            .todo-active {
                background-color: $todo-color;
            }
            .pomodoro-active {
                background-color: $pomodoro-color;
            }
        }
    };
}

